.navigation {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100px;
    padding: $main-padding 0;
    background-color: $second-bgc;
    @include media (xsmall) {
        flex-direction: row;
        height: 100%;
    }
    @include media (small) {
        justify-content: space-between;
    }
    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 $main-padding;
        width: 100%;
        max-width: 260px;
        
    }
    .select {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: nowrap;
        &__display-span {
            font-family: monospace, sans-serif;
            font-size: 1rem;
            padding: $main-padding/2;
            color: #fff;
        }
        &__button {
            justify-content: space-around;
            width: 100%;
            max-width: 260px;
           & button {
               width: 100px;
           }
        }
    }
}